import React from 'react';
import {GridList, GridTile} from 'material-ui/GridList';
import IconButton from 'material-ui/IconButton';
import Subheader from 'material-ui/Subheader';
import StarBorder from 'material-ui/svg-icons/toggle/star-border';

const styles = {
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
  },
  gridList: {
    width: 500,
    height: 450,
    overflowY: 'auto',
  },
};

const tilesData = [
  {
    img: 'http://img0.pclady.com.cn/pclady/1609/03/1575033_3.jpg',
    title: 'Breakfast',
    author: 'jill111',
  },
  {
    img: 'https://imgsa.baidu.com/baike/c0%3Dbaike72%2C5%2C5%2C72%2C24/sign=8b4d1cb6f0246b606f03ba268a917129/32fa828ba61ea8d3c85b36e1910a304e241f58dd.jpg',
    title: 'Tasty burger',
    author: 'pashminu',
  },
  {
    img: 'https://imgsa.baidu.com/baike/c0%3Dbaike92%2C5%2C5%2C92%2C30/sign=1236e71d11dfa9ece9235e4503b99c66/cf1b9d16fdfaaf51133b04be8c5494eef11f7ad3.jpg',
    title: 'Camera',
    author: 'Danson67',
  },
  {
    img: 'https://imgsa.baidu.com/baike/c0%3Dbaike80%2C5%2C5%2C80%2C26/sign=497f3f300bd162d991e36a4e70b6c289/cf1b9d16fdfaaf51b8a9f04b8d5494eef11f7aff.jpg',
    title: 'Morning',
    author: 'fancycrave1',
  },
  {
    img: 'https://imgsa.baidu.com/baike/w%3D268/sign=5d79a126e5dde711e7d244f09fefcef4/cdbf6c81800a19d83425952633fa828ba61e46ea.jpg',
    title: 'Hats',
    author: 'Hans',
  },
  {
    img: 'https://imgsa.baidu.com/baike/c0%3Dbaike150%2C5%2C5%2C150%2C50/sign=0613e42bcf1b9d169eca923392b7dfea/a71ea8d3fd1f4134e7df738a241f95cad0c85e5d.jpg',
    title: 'Honey',
    author: 'fancycravel',
  },
  {
    img: 'https://imgsa.baidu.com/baike/c0%3Dbaike80%2C5%2C5%2C80%2C26/sign=bc524a8709f79052fb124f6c6d9abcaf/d009b3de9c82d15850496025800a19d8bd3e425e.jpg',
    title: 'Vegetables',
    author: 'jill111',
  },
  {
    img: 'https://imgsa.baidu.com/baike/c0%3Dbaike150%2C5%2C5%2C150%2C50/sign=def7ef645bee3d6d36cb8f99227f0647/c8ea15ce36d3d53939caf3003887e950342ab07a.jpg',
    title: 'Water plant',
    author: 'BkrmadtyaKarki',
  },
];

/**
 * A simple example of a scrollable `GridList` containing a [Subheader](/#/components/subheader).
 */
const GridListExampleSimple = () => (
  <div style={styles.root}>
    <GridList
      cellHeight={180}
      style={styles.gridList}
    >
      <Subheader>December</Subheader>
      {tilesData.map((tile) => (
        <GridTile
          key={tile.img}
          title={tile.title}
          subtitle={<span>by <b>{tile.author}</b></span>}
          actionIcon={<IconButton><StarBorder color="white" /></IconButton>}
        >
          <img src={tile.img} />
        </GridTile>
      ))}
    </GridList>
  </div>
);

export default GridListExampleSimple;



